<template>
  <v-container>
    <v-row class="white--text text-center">
      <v-col cols="12">
        <h1>ADMINISTRAR DOCENTES</h1>
      </v-col>
      <v-col cols="12" class="">
        <v-data-table
          :headers="headers"
          :items="desserts"
          sort-by="No_Trabajador"
          class="elevation-1"
          dark
          :search="search"
        >
          <template v-slot:top>
            <v-toolbar flat>
                <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Buscar"
                    single-line
                    hide-details
                    dark
                  ></v-text-field>              
              <v-spacer></v-spacer>
              <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="primary"
                    dark
                    outlined
                    class="mb-2"
                    v-bind="attrs"
                    v-on="on"
                  >
                    Nuevo Docente
                  </v-btn>
                </template>
                <v-card dark>
                  <v-card-title class="text-center">
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12">
                            <p>No De Trabajador</p>
                          <v-otp-input
                            v-model="editedItem.No_Trabajador"
                            :disabled="loading || editedIndex != -1"
                            @finish="onFinish"
                            length="7"         
                            type="number"                   
                          ></v-otp-input>
                          <v-overlay absolute :value="loading">
                            <v-progress-circular
                              indeterminate
                              color="primary"
                            ></v-progress-circular>
                          </v-overlay>
                        </v-col>
                        <v-col cols="12">                            
                          <v-text-field
                            v-model="editedItem.Nombre"
                            label="Nombre"
                            disabled
                          ></v-text-field>
                        </v-col>                        
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" outlined @click="editedItem.No_Trabajador=(editedIndex==-1?'': editedItem.No_Trabajador);close()">
                      Cancel
                    </v-btn>
                    <v-btn color="primary" outlined @click="save">
                      Save
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="500px">
                <v-card dark>
                  <v-card-title class="text-h5"
                    >Quiere borrar a:</v-card-title
                  >
                  <v-card-text>
                      {{editedItem.Nombre}}
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete"
                      >Cancel</v-btn
                    >
                    <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                      >OK</v-btn
                    >
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">
              mdi-pencil
            </v-icon>
            <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
          </template>
          <template v-slot:no-data>
            <v-btn color="primary" @click="initialize"> Reset </v-btn>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  name: "Administrar_Docentes",
  data: () => ({
      search:"",
      loading: false,
      snackbar: false,
      snackbarColor: 'default',
      otp: '',
      text: '',
      expectedOtp: '133707',
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "Nombre",
        align: "start",
        sortable: false,
        value: "Nombre",
      },
      { text: "No Trabajador", value: "No_Trabajador" },  
      { text: "Acciones", value: "actions", sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      Nombre: "",
      No_Trabajador: "",
    },
    defaultItem: {
      Nombre: "",
      No_Trabajador: ""      
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "AÃ±adir Docente" : "Ver Docente";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.desserts = [
        {
          Nombre: "Juan Prez",
          No_Trabajador: "5188987",          
        },
        {
          Nombre: "Ricardo M.",
          No_Trabajador: "5188937",          
        },
        {
          Nombre: "Jose Miranda",
          No_Trabajador: "5188947",          
        }
      ];
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.desserts.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })  
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        this.desserts.push(this.editedItem);
      }
      this.close();
    },
    onFinish (rsp) {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.snackbarColor = (rsp === this.expectedOtp) ? 'success' : 'warning'
          this.text = `Processed OTP with "${rsp}" (${this.snackbarColor})`
          this.snackbar = true
        }, 3500)
    }
  },
};
</script>
